main
{
    width: 90%;
    margin: 30px auto 10px;
    /* min-width: 600px; */
    max-width: 700px;
    position: relative;
}

.toggleBTN
{
    position: absolute;
    right: 0;
    top: -5px;
    width: 115px;
    font-size: 2rem;
    height: 40px;
    /* border-radius: 10px; */
    outline: 3px solid #f90817;
    border: none;
    background-color: transparent;
    color: #f90817;
    transition: 0.2s all ease-in-out;
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
    /* display: none; */
}

.toggleBTN:hover
{
    background-color: #f90817   ;
    color: white;
    outline: none;
    border-radius: 10px;
}

.hideBTN
{
    top: 40px
}

main ul
{
    padding: 40px 0;
}



ul
{
    list-style: none;
}

ul li
{
    position:relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 10px;
    font-size: 25px;
    border: 1px solid rgb(102, 101, 101);
    padding: 10px 20px;
    border-left: 5px solid rgb(81, 81, 236);
}

ul li > div:nth-child(1)
{
    display: flex;
    justify-content: space-between;
    grid-area: b;
}

ul li > div:nth-child(1) span
{
    cursor:pointer;
}

ul li > div:nth-child(2)
{
    display: flex;
    grid-area: a;
    /* gap: 10px; */
}

ul li > div:nth-child(2) p
{
    font-size: 1.3rem;
    font-weight: bold;
}

.taskDisplayingTitle
{
    font-size: 1.4rem;
