/* >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> Medias */ 





@media (max-width: 830px)
{
    main .tasksFilterButtonContainer
    {
        display: flex;
        flex-direction: column;
        grid-gap: 15px;
        gap: 15px;
    }

    main .tasksFilterButtonContainer-upper
    {
        display: flex;
    }

    main .tasksFilterButtonContainer .tasksFilterButtonContainer-lower button
    {
        position: unset;
        width: 100%;
    } 
}


@media (max-width: 700px)
{
        /* >> filtros */

        main .tasksFilterButtonContainer
        {
            display: flex;
            flex-direction: column;
            grid-gap: 15px;
            gap: 15px;
        }
    
        main .tasksFilterButtonContainer .tasksFilterButtonContainer-upper
        {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(3,1fr);
            grid-gap: 15px;
            gap: 15px;
        }
    
        main .tasksFilterButtonContainer .tasksFilterButtonContainer-upper button
        {
            width: 100%;
        } 
    
        main .tasksFilterButtonContainer .tasksFilterButtonContainer-lower
        {
            width: 100%;
        }
    
        main .tasksFilterButtonContainer .tasksFilterButtonContainer-lower button
        {
            position: unset;
            width: 100%;
        } 
}






@media (max-width:550px)
{


    /* >> filtros */

    main .tasksFilterButtonContainer
    {
        display: flex;
        flex-direction: column;
        grid-gap: 15px;
        gap: 15px;
    }

    main .tasksFilterButtonContainer .tasksFilterButtonContainer-upper
    {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-gap: 15px;
        gap: 15px;
    }

    main .tasksFilterButtonContainer .tasksFilterButtonContainer-upper button
    {
        width: 100%;
    } 

    main .tasksFilterButtonContainer .tasksFilterButtonContainer-lower
    {
        width: 100%;
    }

    main .tasksFilterButtonContainer .tasksFilterButtonContainer-lower button
    {
        position: unset;
        width: 100%;
    } 






    /* >> Alineación del contenido en las tasks */
    
    main ul li
    {
        display: grid;
        grid-template-rows: 1fr 3fr;
        grid-template-areas: 
        ". . time"
        "content content content";
    }

    main ul li .li-lower
    {
        grid-area: time;
    }

    main ul li .li-upper
    {
        display: flex;
        flex-direction: column;
        grid-gap: 10px;
        gap: 10px;
        grid-area: content;
    }

    main ul li .li-upper button
    {
        width: 100%;
    }
}











.footer
{
