.addtask
{
    width: 70%;
    margin: 15px auto 15px;
    display: flex;
    justify-content: center;
    /* min-width: 775px; */
}

.addtask form
{
    display: flex;
    align-items: center;
    border: 1px solid gray;
    padding: 15px 54px;
    margin: 0 auto;
    grid-gap: 30px;
    gap: 30px;
}

.addtask form label 
{
    font-size: 3rem;
    margin-right: 10px;
}

.addtask form div
{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
